import { Routes as Reactroutes, Route } from "react-router-dom";
import Home from "./pages/home";
import SellPage from "./SellPage";

function Routes() { 
 
  return (
    <Reactroutes>
        <Route path="/" element={ <Home/> }/> 
         <Route path="/sell" element={<SellPage />}/> 
    </Reactroutes>
  );
}

export default Routes;