import TextField from '@mui/material/TextField';

function SellPage() { 
  return (
    <div className="sell-page">
         <div className="modal-items">
            <div className="modal-header">
                <div className="background-steam-icon"/>
                <span>Ваш инвентарь</span>
            </div>
            <div className="link">
                    <TextField label="Вставьте свою ссылку на обмен" color="primary" className="text-field"/>
                    <button className="save-offer-link">Сохранить</button>
                </div>
                <div className="items">
                    <div className="items-row">
                        <div className="item-card">
                        </div>
                    </div>
                </div>
         </div>

         <div className="modal-items">
            <div className="modal-header">
                <div className="link">
                    <input type="text" className="offer-link"/>
                    <button className="save-offer-link"></button>
                </div>
                <div className="items">
                    <div className="items-row">
                        <div className="item-card">
                        </div>
                    </div>
                </div>
            </div>
         </div>
    </div>
  );
}

export default SellPage;